export class MathHelper {
  static getDistanceWithPoints(x1: number, y1: number, x2: number, y2: number): number {
    const a = Math.abs(x1 - x2)
    const b = Math.abs(y1 - y2)
    const distance = Math.sqrt(a * a + b * b)
    return distance
  }
}



