import { clone } from 'lodash'
import { letterGameConstants } from 'src/actions/constants'
import { StoreAction } from 'src/store/interface'

// import * as Bows from 'bows'

// const log = Bows('letterGameReducer')

export interface LetterGameConfig {
  uppercase: boolean
}

export interface LetterGameState {
  config: LetterGameConfig
  currentLetter: string
}

const initialState: LetterGameState = {
  config: {
    uppercase: true,
  },
  currentLetter: 'a',
}

export function letterGame(state = initialState, action: any): LetterGameState {
  switch (action.type) {
    case letterGameConstants.SET_LETTER:
      return caseSetLetter(state, action)

    default:
      return state
  }
}

// -- Cases

function caseSetLetter(state: LetterGameState, action: StoreAction): LetterGameState {
  const letter: string = (action.payload as any).letter

  const newState: LetterGameState = clone(state)
  newState.currentLetter = letter

  return newState
}



