import * as React from 'react'
import { connect } from 'react-redux'
import { Dispatch } from 'redux'
import styled, { ThemeProvider } from 'styled-components'
import SettingsIcon from '@material-ui/icons/Settings'
import InfoIcon from '@material-ui/icons/Info'
import Page from '@atlaskit/page'
import * as Bows from 'bows'
import { EnvironmentDelegate } from 'src/delegates'
import { StoreAction } from 'src/store/interface'
import { ProgressState, ProfileState } from 'src/reducers'
import OverlayAbout from '../OverlayAbout'
import OverlayConfig from '../OverlayConfig'
import { ProfileAction } from 'src/actions'

const log = Bows('ThemeCyber')

const theme = {}

const MainWrapper = styled.div`
  height: 100%;
  display: flex;
  flex-direction: column;
`

const HeadWrapper = styled.div`
  background-color: #f7f9fa;
`

const HeadContainer = styled.div`
  display: flex;
  align-items: center;
  height: 60px;
  font-weight: bold;
  font-size: 16px;
  // text-align: center;
  margin: 0 auto;
  padding: 0 12px;
  max-width: 600px;
`

const HeadTitle = styled.div`
  flex-grow: 1;
`

const Operators = styled.div``

const Operator = styled.div`
  display: inline-block;
  margin-left: 6px;
  cursor: pointer;
  transition: opacity 0.3s;

  &:hover {
    opacity: 0.7;
  }
`

const ContentWrapper = styled.div`
  flex-grow: 1;
`

const ContentContainer = styled.div`
  height: 100%;
  margin: 0 auto;
  padding: 0 12px;
  max-width: 600px;
`

const FootWrapper = styled.div`
  background-color: #f7f9fa;
`

const FootContainer = styled.div`
  display: flex;
  align-items: center;
  height: 60px;
  margin: 0 auto;
  padding: 0 12px;
  max-width: 600px;
`

interface Props {
  progress: ProgressState
  profile: ProfileState
  setOverlay: (name?: string) => Promise<void>

  displayHeader: boolean
  displayFooter: boolean
}

interface State {
  _placeholder: any
}

const defaultState: State = {
  _placeholder: undefined,
}

class BaseThemeCyber extends React.Component<Props, State> {
  static defaultProps = {
    displayHeader: true,
    displayFooter: false,
  }

  constructor(props: Props) {
    super(props)

    this.state = defaultState
  }

  componentDidMount() {
    log('componentDidMount triggered.')
  }

  componentWillUnmount() {
    log('componentWillUnmount triggered.')
  }

  get title(): string {
    return this.props.profile.title
  }

  get displayConfigOperator(): boolean {
    return false
  }

  get displayInfoOperator(): boolean {
    return false
  }

  configClickHandler(e: any) {
    log('configClickHandler triggered. e:', e)
    this.props.setOverlay('config')
  }

  aboutClickHandler(e: any) {
    log('aboutClickHandler triggered. e:', e)
    this.props.setOverlay('about')
  }

  render(): JSX.Element {
    const restProps = this.props

    return (
      <Page>
        <ThemeProvider theme={theme}>
          <MainWrapper>
            {this.props.displayHeader && this.renderHead()}
            <ContentWrapper>
              <ContentContainer {...restProps} />
            </ContentWrapper>
            {this.props.displayFooter && this.renderFoot()}
            {this.props.profile.overlay === 'config' && this.renderConfig()}
            {this.props.profile.overlay === 'about' && this.renderAbout()}
          </MainWrapper>
        </ThemeProvider>
      </Page>
    )
  }

  private renderHead(): JSX.Element {
    return (
      <HeadWrapper>
        <HeadContainer>
          <HeadTitle>{this.title}</HeadTitle>
          <Operators>
            {this.displayConfigOperator && (
              <Operator onClick={this.configClickHandler.bind(this)}>
                <SettingsIcon />
              </Operator>
            )}
            {this.displayInfoOperator && (
              <Operator onClick={this.aboutClickHandler.bind(this)}>
                <InfoIcon />
              </Operator>
            )}
          </Operators>
        </HeadContainer>
      </HeadWrapper>
    )
  }

  private renderFoot(): JSX.Element {
    return (
      <FootWrapper>
        <FootContainer>{EnvironmentDelegate.Version}</FootContainer>
      </FootWrapper>
    )
  }

  private renderConfig(): JSX.Element {
    return (
      <OverlayConfig
        onClose={() => {
          this.props.setOverlay()
        }}
      />
    )
  }

  private renderAbout(): JSX.Element {
    return (
      <OverlayAbout
        onClose={() => {
          this.props.setOverlay()
        }}
      />
    )
  }
}

function mapStateToProps(state: StoreAction) {
  const { progress, profile } = state
  return {
    progress,
    profile,
  }
}

function mapDispatchToProps(dispatch: Dispatch) {
  return {
    setOverlay: async (name?: string): Promise<void> => {
      dispatch(ProfileAction.setOverlay(name))
    },
  }
}

const ThemeCyber = connect(
  mapStateToProps,
  mapDispatchToProps
)(BaseThemeCyber)
export default ThemeCyber



