import { random, round } from 'lodash'
import owlSamsungImage from '../assets/images/emojis/owl-samsung.png'
import dogTwitterImage from '../assets/images/emojis/dog-twitter.png'
import catTwitterImage from '../assets/images/emojis/cat-twitter.png'
import horseFacebookImage from '../assets/images/emojis/horse-facebook.png'
import snakeGoogleImage from '../assets/images/emojis/snake-google.png'
import birdFacebookImage from '../assets/images/emojis/bird-facebook.png'
import crocodileSamsungImage from '../assets/images/emojis/crocodile-samsung.png'
import elephantGoogleImage from '../assets/images/emojis/elephant-google.png'
import fishFacebookImage from '../assets/images/emojis/fish-facebook.png'
import frogFaceTwitterImage from '../assets/images/emojis/frog-face-twitter.png'
import kangarooFacebookImage from '../assets/images/emojis/kangaroo-facebook.png'
import sheepFacebookImage from '../assets/images/emojis/sheep-facebook.png'

const emojis = [
  owlSamsungImage,
  dogTwitterImage,
  catTwitterImage,
  horseFacebookImage,
  snakeGoogleImage,
  birdFacebookImage,
  crocodileSamsungImage,
  elephantGoogleImage,
  fishFacebookImage,
  frogFaceTwitterImage,
  kangarooFacebookImage,
  sheepFacebookImage,
]
const colors = [
  'red',
  'green',
  'blue',
  'white',
  'black',
  'gray',
  'yellow',
  'orange',
  'purple',
  'pink',
  'brown',
  // 'magenta',
  // 'indigo', // rainbow
  // 'violet', // rainbow
  // 'cyan',
]
const alphabets = 'abcdefghijklmnopqrstuvwxyz'
const decimals = '1234567890'

export class RandomHelper {
  static getRandomNumber(min = 0, max = 1, precision = 0): number {
    const output = round(random(min, max, true), precision)
    return output
  }

  static getRandomEmoji(): string {
    const randomIndex = random(0, emojis.length - 1)
    return emojis[randomIndex]
  }

  static getRandomColor(): string {
    const randomIndex = random(0, colors.length - 1)
    return colors[randomIndex]
  }

  static getRandomAlphabet(uppercase = false): string {
    const randomIndex = random(0, alphabets.length - 1)
    const letter = alphabets[randomIndex]

    if (uppercase) {
      return letter.toUpperCase()
    } else {
      return letter.toLowerCase()
    }
  }

  static getRandomDecimal(): string {
    const randomIndex = random(0, decimals.length - 1)
    return decimals[randomIndex]
  }
}



