import * as React from 'react'
import { connect } from 'react-redux'
import { Dispatch } from 'redux'
import styled from 'styled-components'
import Button from '@atlaskit/button'
import * as Bows from 'bows'
import ThemeCyber from './ThemeCyber/ThemeCyber'
import { DomHelper } from 'src/helpers'
import { StoreAction } from 'src/store/interface'
import { ProgressState, ProfileState, LetterGameState } from 'src/reducers'
import { ProfileAction, LetterGameAction } from 'src/actions'
import { RandomHelper } from 'src/helpers/random-helper'

const COMPONENT_NAME = 'GameLetter'
const log = Bows(COMPONENT_NAME)

const ComponentWrapper = styled.div`
  display: flex;
  flex-direction: column;
  height: 100%;
`

const SandboxWrapper = styled.div`
  flex-grow: 1;
  margin: 12px 0;
  overflow: hidden;
  position: relative;
`

const LetterWrapper = styled.div`
  font-weight: bold;
  text-align: center;
  // margin-top: 24px;
  font-size: 180px;
`

const OperatorWrapper = styled.div`
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 12px;
`

interface Props {
  progress: ProgressState
  profile: ProfileState
  letterGame: LetterGameState
  setTitle: (title: string) => Promise<void>
  setLetter: (letter: string) => Promise<void>
}

interface State {
  _placeholder: any
}

const defaultState: State = {
  _placeholder: undefined,
}

class BaseGameLetter extends React.Component<Props, State> {
  static defaultProps = {}

  constructor(props: Props) {
    super(props)

    this.state = defaultState
  }

  componentDidMount() {
    log('componentDidMount triggered.')

    this.props.setTitle(this.title)
    DomHelper.setTitle(this.title)
    this.setLetter()
  }

  componentWillUnmount() {
    log('componentWillUnmount triggered.')
  }

  get title(): string {
    return 'Name a Letter - PocketLearn'
  }

  render(): JSX.Element {
    return (
      <ThemeCyber>
        <ComponentWrapper>
          {this.renderSandbox()}
          {this.renderOperator()}
        </ComponentWrapper>
      </ThemeCyber>
    )
  }

  nextClickHandler(e: any) {
    log('nextClickHandler triggered. e:', e)
    this.setLetter()
  }

  private renderSandbox(): JSX.Element {
    return (
      <SandboxWrapper>
        <LetterWrapper>{this.props.letterGame.currentLetter}</LetterWrapper>
      </SandboxWrapper>
    )
  }

  private renderOperator(): JSX.Element {
    return (
      <OperatorWrapper>
        <Button appearance="primary" shouldFitContainer onClick={this.nextClickHandler.bind(this)}>
          Next
        </Button>
      </OperatorWrapper>
    )
  }

  private setLetter() {
    const previousLetter = this.props.letterGame.currentLetter
    let letter = RandomHelper.getRandomAlphabet(this.props.letterGame.config.uppercase)
    while (previousLetter === letter) {
      letter = RandomHelper.getRandomAlphabet(this.props.letterGame.config.uppercase)
    }

    this.props.setLetter(letter)
  }
}

function mapStateToProps(state: StoreAction) {
  const { progress, profile, letterGame } = state
  return {
    progress,
    profile,
    letterGame,
  }
}

function mapDispatchToProps(dispatch: Dispatch) {
  return {
    setTitle: async (title: string): Promise<void> => {
      dispatch(ProfileAction.setTitle(title))
    },

    setLetter: async (letter: string): Promise<void> => {
      dispatch(LetterGameAction.setLetter(letter))
    },
  }
}

const GameLetter = connect(
  mapStateToProps,
  mapDispatchToProps
)(BaseGameLetter)
export default GameLetter



