import { combineReducers } from 'redux'
import { progress } from './progress-reducer'
import { profile } from './profile-reducer'
import { countingGame } from './counting-game-reducer'
import { colorGame } from './color-game-reducer'
import { letterGame } from './letter-game-reducer'

export const rootReducer = combineReducers({
  progress,
  profile,
  countingGame,
  colorGame,
  letterGame,
})



