import { clone } from 'lodash'
import { colorGameConstants } from 'src/actions/constants/color-game'
import { StoreAction } from 'src/store/interface'

// import * as Bows from 'bows'

// const log = Bows('colorGameReducer')

export interface ColorGameConfig {
  _placeholder?: any
}

export interface ColorGameState {
  config: ColorGameConfig
  currentColor: string
}

const initialState: ColorGameState = {
  config: {},
  currentColor: 'beige',
}

export function colorGame(state = initialState, action: any): ColorGameState {
  switch (action.type) {
    case colorGameConstants.SET_COLOR:
      return caseSetColor(state, action)

    default:
      return state
  }
}

// -- Cases

function caseSetColor(state: ColorGameState, action: StoreAction): ColorGameState {
  const color: string = (action.payload as any).color

  const newState: ColorGameState = clone(state)
  newState.currentColor = color

  return newState
}



