import * as React from 'react'
import { connect } from 'react-redux'
import { Dispatch } from 'redux'
import styled from 'styled-components'
import Button from '@atlaskit/button'
import * as Bows from 'bows'
import ThemeCyber from './ThemeCyber/ThemeCyber'
import { RandomHelper } from 'src/helpers/random-helper'
import { CountItem } from '../common/interfaces'
import { MathHelper, DomHelper } from 'src/helpers'
import { StoreAction } from 'src/store/interface'
import { ProgressState, ProfileState, CountingGameState } from 'src/reducers'
import { ProfileAction, CountingGameAction } from 'src/actions'

const COMPONENT_NAME = 'GameCounting'
const log = Bows(COMPONENT_NAME)

const ComponentWrapper = styled.div`
  display: flex;
  flex-direction: column;
  height: 100%;
`

const SandboxWrapper = styled.div`
  flex-grow: 1;
  background-color: rgba(7, 71, 166, 0.05);
  margin: 12px 0;
  overflow: hidden;
  position: relative;
`

const DiagramContainer = styled.div``

const ItemWrapper = styled.div`
  // border: solid 1px blue;
  position: absolute;
  width: 48px;
  height: 48px;

  img {
    width: 100%;
  }
`

const OperatorWrapper = styled.div`
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 12px;
`

interface Props {
  progress: ProgressState
  profile: ProfileState
  countingGame: CountingGameState
  setTitle: (title: string) => Promise<void>
  setCountItems: (countItems: CountItem[]) => Promise<void>
}

interface State {
  _placeholder: any
}

const defaultState: State = {
  _placeholder: undefined,
}

class BaseGameCounting extends React.Component<Props, State> {
  static defaultProps = {}

  constructor(props: Props) {
    super(props)

    this.state = defaultState
  }

  componentDidMount() {
    log('componentDidMount triggered.')

    this.props.setTitle(this.title)
    DomHelper.setTitle(this.title)
    this.setCountItems()
  }

  componentWillUnmount() {
    log('componentWillUnmount triggered.')
  }

  get title(): string {
    return 'Counting Game - PocketLearn'
  }

  render(): JSX.Element {
    return (
      <ThemeCyber>
        <ComponentWrapper>
          {this.renderSandbox()}
          {this.renderOperator()}
        </ComponentWrapper>
      </ThemeCyber>
    )
  }

  nextClickHandler(e: any) {
    log('nextClickHandler triggered. e:', e)
    this.setCountItems()
  }

  private renderSandbox(): JSX.Element {
    return (
      <SandboxWrapper>
        <DiagramContainer>{this.props.countingGame.countItems.map((item) => this.renderItem(item))}</DiagramContainer>
      </SandboxWrapper>
    )
  }

  private renderItem(item: CountItem): JSX.Element {
    const itemStyle = {
      top: item.y * 100 + '%',
      left: item.x * 100 + '%',
    }

    return (
      <ItemWrapper key={item.index} style={itemStyle}>
        <img src={item.imagePath} alt="" />
      </ItemWrapper>
    )
  }

  private renderOperator(): JSX.Element {
    return (
      <OperatorWrapper>
        <Button appearance="primary" shouldFitContainer onClick={this.nextClickHandler.bind(this)}>
          Next
        </Button>
      </OperatorWrapper>
    )
  }

  private setCountItems() {
    const count = RandomHelper.getRandomNumber(this.props.countingGame.config.minCount, this.props.countingGame.config.maxCount)
    const emoji = RandomHelper.getRandomEmoji()
    const items: CountItem[] = []

    for (let i = 0; i < count; i++) {
      const item = this.generateCountItem(i, emoji, items, this.props.countingGame.config.minDistanceBetweenItems)
      items.push(item)
    }

    this.props.setCountItems(items)
  }

  private generateCountItem(index: number, imagePath: string, existingItems: CountItem[], minDistance: number): CountItem {
    // log('generateCountItem triggered.')
    // log('index:', index)

    let x: number
    let y: number
    let isFind = false
    const loopBreakAt = 20
    let i = 0

    while (!isFind) {
      x = RandomHelper.getRandomNumber(0, 0.9, 4)
      y = RandomHelper.getRandomNumber(0, 0.9, 4)
      isFind = this.isAcceptablePoint(x, y, existingItems, minDistance)

      // Prevent lengthy loop
      if (i >= loopBreakAt) {
        // log('loop breaker triggered!')
        isFind = true
      } else {
        i += 1
      }
    }

    const item: CountItem = {
      index,
      imagePath,
      y: RandomHelper.getRandomNumber(0, 0.9, 4),
      x: RandomHelper.getRandomNumber(0, 0.9, 4),
    }
    return item
  }

  private isAcceptablePoint(x: number, y: number, existingItems: CountItem[], minDistance: number): boolean {
    for (const item of existingItems) {
      const distance = MathHelper.getDistanceWithPoints(x, y, item.x, item.y)
      // log(`[${x}, ${y}] [${item.x}, ${item.y}]`, 'distance:', distance)
      if (distance < minDistance) {
        // log('no good! try again!')
        return false
      }
    }
    return true
  }
}

function mapStateToProps(state: StoreAction) {
  const { progress, profile, countingGame } = state
  return {
    progress,
    profile,
    countingGame,
  }
}

function mapDispatchToProps(dispatch: Dispatch) {
  return {
    setTitle: async (title: string): Promise<void> => {
      dispatch(ProfileAction.setTitle(title))
    },

    setCountItems: async (countItems: CountItem[]): Promise<void> => {
      dispatch(CountingGameAction.setCountItems(countItems))
    },
  }
}

const GameCounting = connect(
  mapStateToProps,
  mapDispatchToProps
)(BaseGameCounting)
export default GameCounting



