import * as React from 'react'
import { connect } from 'react-redux'
import { Dispatch } from 'redux'
import styled from 'styled-components'
// import Button from '@atlaskit/button'
import * as Bows from 'bows'
import ThemeCyber from './ThemeCyber/ThemeCyber'
import { DomHelper } from 'src/helpers'
import { StoreAction } from 'src/store/interface'
import { ProgressState, ProfileState } from 'src/reducers'
import { ProfileAction } from 'src/actions'
import { Button } from '@atlaskit/button/components/Button'
import { NavDelegate } from 'src/delegates'

const COMPONENT_NAME = 'Landing'
const log = Bows(COMPONENT_NAME)

const ComponentWrapper = styled.div`
  // display: flex;
  // flex-direction: column;
  // height: 100%;
  text-align: center;
`

interface Props {
  progress: ProgressState
  profile: ProfileState
  setTitle: (title: string) => Promise<void>
}

interface State {
  _placeholder: any
}

const defaultState: State = {
  _placeholder: undefined,
}

class BaseLanding extends React.Component<Props, State> {
  static defaultProps = {}

  constructor(props: Props) {
    super(props)

    this.state = defaultState
  }

  componentDidMount() {
    log('componentDidMount triggered.')

    this.props.setTitle(this.title)
    DomHelper.setTitle(this.title)
  }

  componentWillUnmount() {
    log('componentWillUnmount triggered.')
  }

  get title(): string {
    return 'PocketLearn'
  }

  render(): JSX.Element {
    return (
      <ThemeCyber>
        <ComponentWrapper>
          <br />
          <Button appearance="primary" onClick={() => NavDelegate.to('/count')}>
            Counting Game
          </Button>
          <br />

          <br />
          <Button appearance="primary" onClick={() => NavDelegate.to('/color')}>
            Color Game
          </Button>
          <br />

          <br />
          <Button appearance="primary" onClick={() => NavDelegate.to('/letter')}>
            Letter Game
          </Button>
          <br />
        </ComponentWrapper>
      </ThemeCyber>
    )
  }
}

function mapStateToProps(state: StoreAction) {
  const { progress, profile } = state
  return {
    progress,
    profile,
  }
}

function mapDispatchToProps(dispatch: Dispatch) {
  return {
    setTitle: async (title: string): Promise<void> => {
      dispatch(ProfileAction.setTitle(title))
    },
  }
}

const Landing = connect(
  mapStateToProps,
  mapDispatchToProps
)(BaseLanding)
export default Landing



