import { StoreAction } from 'src/store/interface'
import { profileConstants } from './constants'

export class ProfileAction {
  static setTitle(title: string): StoreAction {
    return { type: profileConstants.SET_TITLE, payload: { title } }
  }

  static setOverlay(name?: string): StoreAction {
    return { type: profileConstants.SET_OVERLAY, payload: { overlay: name } }
  }
}



