import * as React from 'react'
import { connect } from 'react-redux'
import { Dispatch } from 'redux'
import styled from 'styled-components'
import Button from '@atlaskit/button'
import { capitalize } from 'lodash'
import * as Bows from 'bows'
import ThemeCyber from './ThemeCyber/ThemeCyber'
// import { RandomHelper } from 'src/helpers/random-helper'
// import { CountItem } from '../common/interfaces'
import { DomHelper } from 'src/helpers'
import { StoreAction } from 'src/store/interface'
import { ProgressState, ProfileState, ColorGameState } from 'src/reducers'
import { ProfileAction } from 'src/actions'
import { RandomHelper } from 'src/helpers/random-helper'
import { ColorGameAction } from 'src/actions/color-game-action'

const COMPONENT_NAME = 'GameColor'
const log = Bows(COMPONENT_NAME)

const ComponentWrapper = styled.div`
  display: flex;
  flex-direction: column;
  height: 100%;
`

const SandboxWrapper = styled.div`
  flex-grow: 1;
  background-color: rgba(7, 71, 166, 0.05);
  // background-color: rgba(0, 0, 0, 0.1);
  background-image: repeating-linear-gradient(45deg, transparent, transparent 40px, rgba(0, 0, 0, 0.025) 40px, rgba(0, 0, 0, 0.025) 80px);
  margin: 12px 0;
  overflow: hidden;
  position: relative;
`

const ColorBox = styled.div`
  position: relative;
  width: 80%;
  margin: 24px auto;
  // background-color: beige;
  border-radius: 12px;

  &:after {
    content: '';
    display: block;
    padding-bottom: 100%;
  }
`

const ColorContent = styled.div`
  position: absolute;
  width: 100%;
  height: 100%;

  display: flex;
  align-items: center;
`

const ColorLabel = styled.div`
  // background-color: rgba(255, 255, 255, 0.1);
  font-size: 24px;
  color: white;
  text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.2);
  width: 100%;
  text-align: center;
  opacity: 0.5;
`

const OperatorWrapper = styled.div`
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 12px;
`

interface Props {
  progress: ProgressState
  profile: ProfileState
  colorGame: ColorGameState
  setTitle: (title: string) => Promise<void>
  setColor: (color: string) => Promise<void>
}

interface State {
  _placeholder: any
}

const defaultState: State = {
  _placeholder: undefined,
}

class BaseGameColor extends React.Component<Props, State> {
  static defaultProps = {}

  constructor(props: Props) {
    super(props)

    this.state = defaultState
  }

  componentDidMount() {
    log('componentDidMount triggered.')

    this.props.setTitle(this.title)
    DomHelper.setTitle(this.title)
    this.setColor()
  }

  componentWillUnmount() {
    log('componentWillUnmount triggered.')
  }

  get title(): string {
    return 'Name a Colour - PocketLearn'
  }

  render(): JSX.Element {
    return (
      <ThemeCyber>
        <ComponentWrapper>
          {this.renderSandbox()}
          {this.renderOperator()}
        </ComponentWrapper>
      </ThemeCyber>
    )
  }

  nextClickHandler(e: any) {
    log('nextClickHandler triggered. e:', e)
    this.setColor()
  }

  private renderSandbox(): JSX.Element {
    const boxStyle = {
      backgroundColor: this.props.colorGame.currentColor,
    }
    const colorLabel = capitalize(this.props.colorGame.currentColor)

    return (
      <SandboxWrapper>
        <ColorBox style={boxStyle}>
          <ColorContent>
            <ColorLabel>{colorLabel}</ColorLabel>
          </ColorContent>
        </ColorBox>
      </SandboxWrapper>
    )
  }

  private renderOperator(): JSX.Element {
    return (
      <OperatorWrapper>
        <Button appearance="primary" shouldFitContainer onClick={this.nextClickHandler.bind(this)}>
          Next
        </Button>
      </OperatorWrapper>
    )
  }

  private setColor() {
    const currentColor = this.props.colorGame.currentColor
    let color = RandomHelper.getRandomColor()
    while (currentColor === color) {
      // Pick another color if new color remains unchanged
      color = RandomHelper.getRandomColor()
    }

    this.props.setColor(color)
  }
}

function mapStateToProps(state: StoreAction) {
  const { progress, profile, colorGame } = state
  return {
    progress,
    profile,
    colorGame,
  }
}

function mapDispatchToProps(dispatch: Dispatch) {
  return {
    setTitle: async (title: string): Promise<void> => {
      dispatch(ProfileAction.setTitle(title))
    },

    setColor: async (color: string): Promise<void> => {
      dispatch(ColorGameAction.setColor(color))
    },
  }
}

const GameColor = connect(
  mapStateToProps,
  mapDispatchToProps
)(BaseGameColor)
export default GameColor



