import { clone } from 'lodash'
import { CountItem } from 'src/common/interfaces'
import { countingGameConstants } from 'src/actions'
import { StoreAction } from 'src/store/interface'

// import * as Bows from 'bows'

// const log = Bows('countingGameReducer')

export interface CountingGameConfig {
  minCount: number
  maxCount: number
  minDistanceBetweenItems: number
}

export interface CountingGameState {
  config: CountingGameConfig
  countItems: CountItem[]
}

const initialState: CountingGameState = {
  config: {
    minCount: 1,
    maxCount: 10,
    minDistanceBetweenItems: 0.25,
  },
  countItems: [],
}

export function countingGame(state = initialState, action: any): CountingGameState {
  switch (action.type) {
    case countingGameConstants.SET_COUNT_ITEMS:
      return caseSetCountItems(state, action)

    default:
      return state
  }
}

// -- Cases

function caseSetCountItems(state: CountingGameState, action: StoreAction): CountingGameState {
  const countItems: CountItem[] = (action.payload as any).countItems

  const newState: CountingGameState = clone(state)
  newState.countItems = countItems

  return newState
}



