import * as React from 'react'
import CloseIcon from '@material-ui/icons/Close'
import styled from 'styled-components'
import * as Bows from 'bows'

const COMPONENT_NAME = 'OverlayAbout'
const log = Bows(COMPONENT_NAME)

const ComponentWrapper = styled.div`
  background-color: #333366;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
`

const ComponentInnerWrapper = styled.div`
  color: #f4f5f7;
`

const HeadWrapper = styled.div`
  display: flex;
  align-items: center;
  height: 60px;
  font-weight: bold;
  font-size: 16px;
  // text-align: center;
  margin: 0 auto;
  padding: 0 12px;
  max-width: 600px;
`

const HeadTitle = styled.div`
  flex-grow: 1;
`

const Operator = styled.div`
  margin-left: 6px;
  cursor: pointer;
  transition: opacity 0.3s;

  &:hover {
    opacity: 0.7;
  }
`

const ContentWrapper = styled.div`
  margin: 0 auto;
  padding: 0 12px;
  max-width: 600px;
`

interface Props {
  _placeholder: any

  onClose: any
}

interface State {
  _placeholder: any
}

const defaultState: State = {
  _placeholder: undefined,
}

export default class OverlayAbout extends React.Component<Props, State> {
  static defaultProps = {
    _placeholder: undefined,
  }

  constructor(props: Props) {
    super(props)

    this.state = defaultState
  }

  componentDidMount() {
    log('componentDidMount triggered.')
  }

  componentWillUnmount() {
    log('componentWillUnmount triggered.')
  }

  closeClickHandler(e: any) {
    log('closeClickHandler triggered. e:', e)
  }

  render(): JSX.Element {
    return (
      <ComponentWrapper>
        <ComponentInnerWrapper>
          {this.renderHead()}
          <ContentWrapper>
            <p>
              Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo
              consequat.
            </p>
            <p>Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.</p>
          </ContentWrapper>
        </ComponentInnerWrapper>
      </ComponentWrapper>
    )
  }

  private renderHead(): JSX.Element {
    return (
      <HeadWrapper>
        <HeadTitle>About This App</HeadTitle>
        <Operator onClick={() => this.props.onClose()}>
          <CloseIcon />
        </Operator>
      </HeadWrapper>
    )
  }
}



