import { clone } from 'lodash'
// import * as Bows from 'bows'
import { StoreAction } from '../store/interface'
import { profileConstants } from 'src/actions'

// const log = Bows('profileReducer')

export interface ProfileState {
  title: string
  overlay?: string
}

const initialState: ProfileState = {
  title: 'PocketLearn',
  overlay: undefined,
}

export function profile(state = initialState, action: StoreAction): ProfileState {
  switch (action.type) {
    case profileConstants.SET_TITLE:
      return caseSetTitle(state, action)

    case profileConstants.SET_OVERLAY:
      return caseSetOverlay(state, action)

    default:
      return state
  }
}

// -- Cases

function caseSetTitle(state: ProfileState, action: StoreAction): ProfileState {
  const title: string = (action.payload as any).title

  const newState: ProfileState = clone(state)
  newState.title = title

  return newState
}

function caseSetOverlay(state: ProfileState, action: StoreAction): ProfileState {
  const overlay: string | undefined = (action.payload as any).overlay

  const newState: ProfileState = clone(state)
  newState.overlay = overlay

  return newState
}



