import { StoreAction } from 'src/store/interface'
import { countingGameConstants } from './constants'
import { CountItem } from 'src/common/interfaces'

export class CountingGameAction {
  static setCountItems(countItems: CountItem[]): StoreAction {
    return { type: countingGameConstants.SET_COUNT_ITEMS, payload: { countItems } }
  }
}



